content .dots-wrapper .hollow-dots-spinner .dot{width:15px;height:15px;margin:0 calc(15px / 2);border:calc(15px / 5) solid #6c6ce5;border-radius:50%;float:left;-webkit-transform:scale(0);transform:scale(0);-webkit-animation:hollow-dots-spinner-animation 1000ms ease infinite 0ms;animation:hollow-dots-spinner-animation 1000ms ease infinite 0ms}.box-content .dots-wrapper .hollow-dots-spinner .dot:nth-child(1){-webkit-animation-delay:calc(300ms * 1);animation-delay:calc(300ms * 1)}.box-content .dots-wrapper .hollow-dots-spinner .dot:nth-child(2){-webkit-animation-delay:calc(300ms * 2);animation-delay:calc(300ms * 2)}.box-content .dots-wrapper .hollow-dots-spinner .dot:nth-child(3){-webkit-animation-delay:calc(300ms * 3);animation-delay:calc(300ms * 3)}.box-content .button-link{margin:0 auto;margin-top:0.5rem;font-size:1.5rem;font-weight:600;letter-spacing:0.1px;color:#fff;background-color:#ff4c60;padding:1.2rem 3rem;border-radius:3rem;font-family:"Quicksand", sans-serif;display:block;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;-webkit-transform:perspective(1px);transform:perspective(1px);box-shadow:0 0 1px rgba(0,0,0,0);cursor:pointer;outline:none}.box-content .button-link:hover{-webkit-animation-name:button-push;animation-name:button-push;-webkit-animation-duration:0.3s;animation-duration:0.3s;-webkit-animation-timing-function:linear;animation-timing-function:linear;-webkit-animation-iteration-count:1;animation-iteration-count:1}.box-content .details-wrapper{position:absolute;left:5px;bottom:5px}.box-content .details-wrapper p{font-size:1rem;font-weight:400;color:#8b949e;letter-spacing:0.5px}.box-content .details-wrapper p span{font-size:1.2rem;color:#c9d1d9;font-weight:700}.box-content .details-wrapper p:last-child{margin-top:0.2rem}@media screen and (min-width: 1025px){.box-content{border-radius:0}}

